<template>
    <div>
        <loading :active.sync="isLoading"/>
        <div class="text-right mt-4">
            <button class="btn btn-primary" @click.prevent="showModal(true)"></button>
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th width="120"></th>
                    <th></th>
                    <th width="120"></th>
                    <th width="120"></th>
                    <th width="100"></th>
                    <th width="90"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in products" :key="item.id">
                    <td>
                        {{ item.category }}
                    </td>
                    <td>
                        {{ item.title }}
                    </td>
                    <td class="text-right">
                        {{ item.origin_price | currency }}
                    </td>
                    <td class="text-right">
                        {{ item.price | currency }}
                    </td>
                    <td>
                        <span v-if="item.is_enabled" class="text-success"></span>
                        <span v-else></span>
                    </td>
                    <td>
                        <div class="row align-items-end">
                            <button class="btn btn-outline-primary btn-sm" @click.prevent="showModal(false, item)"></button>
                            <button class="btn btn-outline-danger btn-sm" @click.prevent="deleteProduct(item)"></button>
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
        <!--  -->
        <Page :page-info="pagination" @changePage="getProducts" />
        <!-- Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-4">
                                <div class="form-group">
                                    <label for="image"></label>
                                    <input type="text" v-model="tempProduct.imageUrl" class="form-control" 
                                        id="image" placeholder="">
                                </div>
                                <div class="form-group">
                                    <label for="customFile"> 
                                        <i class="fas fa-spinner fa-spin" v-if="isLoading"></i>
                                    </label>
                                    <input type="file" id="customFile" class="form-control"
                                        ref="files" @change="uploadImg">
                                </div>
                                <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                                    class="img-fluid" :src="tempProduct.imageUrl" alt="">
                            </div>
                            <div class="col-sm-8">
                                <div class="form-group">
                                    <label for="title"></label>
                                    <input type="text" v-model="tempProduct.title" class="form-control"
                                        id="title" placeholder="">
                                </div>

                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="category"></label>
                                        <input type="text" v-model="tempProduct.category" class="form-control" 
                                            id="category"  placeholder="">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price"></label>
                                        <input type="unit" v-model="tempProduct.unit" class="form-control" 
                                            id="unit" placeholder="">
                                    </div>
                                </div>

                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="origin_price"></label>
                                        <input type="number" v-model="tempProduct.origin_price" class="form-control" 
                                            id="origin_price" placeholder="">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="price"></label>
                                        <input type="number" v-model="tempProduct.price" class="form-control" 
                                            id="price" placeholder="">
                                    </div>
                                </div>
                                <hr>

                                <div class="form-group">
                                    <label for="description"></label>
                                    <textarea type="text" v-model="tempProduct.description" class="form-control" 
                                        id="description" placeholder=""></textarea>
                                </div>
                                <div class="form-group">
                                    <label for="content"></label>
                                    <textarea type="text" v-model="tempProduct.content" class="form-control" 
                                        id="content" placeholder=""></textarea>
                                </div>
                                <div class="form-group">
                                    <div class="form-check">
                                        <input class="form-check-input" type="checkbox"
                                            id="is_enabled" 
                                             v-model="tempProduct.is_enabled"
                                             :true-value="1"
                                             :false-value="0">
                                        <label class="form-check-label" for="is_enabled">
                                            
                                        </label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-primary" @click.prevent="updateProducts"></button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-danger text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                         <strong class="text-danger">{{ tempProduct.title }}</strong> ()
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-danger" @click.prevent="isDelete = true, updateProducts()">
                            </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
import Page from '../Page';

export default {
    components: {
        Page,
    },
    data() {
        return {
            products: [],
            tempProduct: {},
            httpMethod: 'post',
            inNew: false,
            isDelete: false,
            isLoading: false,
            pagination: {},
        };
    },
    methods: {
        getProducts(page = 1) {
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.API}/products?page=${page}`;
            vm.isLoading = true;
            vm.$http.get(api).then((response) => {
                console.log(response.data);
                vm.isLoading = false;
                vm.products = response.data.products;
                vm.pagination = response.data.pagination;
            });
        },
        showModal(isNew, item) {
            $('#productModal').modal('show');
            const vm = this;
            if(isNew){
                vm.tempProduct = {};
                vm.isNew = true;
            }else{
                vm.tempProduct = Object.assign({}, item);
                vm.isNew = false;
            }
        },
        updateProducts() {
            const vm = this;
            let api = `${process.env.APIPATH}/api/${process.env.API}/admin/product`;

            if(vm.isDelete){
                api = `${process.env.APIPATH}/api/${process.env.API}/admin/product/${vm.tempProduct.id}`;
                console.log(api);
                vm.$http.delete(api).then((response) => {
                    console.log(response.data);
                    if(response.data.success){
                        vm.getProducts();
                    }else{
                        console.log(' : ' + response.data.message);
                        this.$bus.$emit('message:push', ' : ' + response.data.message, 'danger');
                    }
                    $('#delProductModal').modal('hide');
                });
            }else if(!vm.isNew){
                api = `${process.env.APIPATH}/api/${process.env.API}/admin/product/${vm.tempProduct.id}`;
                vm.httpMethod = 'put';
            }

            if(!vm.isDelete){
                vm.$http[vm.httpMethod](api, { data: vm.tempProduct }).then((response) => {
                    console.log(response.data);
                    if(response.data.success){
                        vm.getProducts();
                    }else{
                        if(vm.httpMethod === 'put'){
                            console.log(' : ' + response.data.message);
                            this.$bus.$emit('message:push', ' : ' + response.data.message, 'danger');
                        }else{
                            console.log(' : ' + response.data.message);
                            this.$bus.$emit('message:push', ' : ' + response.data.message, 'danger');
                        }
                    }
                    $('#productModal').modal('hide');
                });
            }
        },
        deleteProduct(item) {
            $('#delProductModal').modal('show');
            const vm = this;
            vm.tempProduct = Object.assign({}, item);
            console.log(vm.tempProduct);
        },
        uploadImg() {
            console.log(this);
            const uploadFile = this.$refs.files.files[0];
            const vm = this;
            const formData = new FormData();
            formData.append('file-to-upload', uploadFile);
            vm.isLoading = true;
            const url = `${process.env.APIPATH}/api/${process.env.API}/admin/upload`;
            vm.$http.post(url, formData, {
                headers: {
                    'Content-type': 'multipart/form-data',
                }
            }).then((response) => {
                vm.isLoading = false;
                console.log(response.data);
                if(response.data.success){
                    // vm.tempProduct.imageUrl = response.data.imageUrl;
                    vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);
                }else{
                    console.log(' : ' + response.data.message);
                    this.$bus.$emit('message:push', ' : ' + response.data.message, 'danger');
                }
            });
        },
    },
    created() {
        this.getProducts();
    },
}
</script>



